import request from "./request";

/**
 * @name 登录
 */
export const requestLogin = (params?: any) => {
  return request('post', '/api/admin/system/toLogin/phone', params)
}

/**
 * @name 获取企业信息
 */
export const getEnterpriseInfo = () => {
  return request('get', '/api/admin/system/userInfo')
}
