import axios from "axios";

// 设置请求头
let headers: any = {}
const setToken = (): void => {
  if(localStorage.getItem('_token')) {
    headers.Authorization = 'Bearer ' + localStorage.getItem('_token')
  } else {
    window.location.hash = '#/login'
  }
}
setToken()
//过滤请求
axios.interceptors.request.use(
  config => {
    setToken()
    config.headers = headers;
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);
// 添加响应拦截器
axios.interceptors.response.use(
  response => {
    if (response.data.code === 200) {
      return Promise.resolve(response.data);
    } else {
      return Promise.resolve(response.data);
    }
  },
  error => {
    // if(error.toString().includes('401')) {
    //   window.location.href = 'https://deve.svmeet.com/#/'
    // }
    if (error && error.response) {
      let res = {code: null};
      res.code = error.response.status;
    //   res.msg = throwErr(error.response.status, error.response); //throwErr 捕捉服务端的http状态码 定义在utils工具类的方法
      return Promise.reject(res);
    }
    return Promise.reject(error);
  }
);
export default function request(method: any, url: any, data?: any) {
  //暴露 request 给我们好API 管理
  method = method.toLocaleLowerCase(); //封装RESTful API的各种请求方式 以 post get delete为例
  if (method === "post") {
    return axios.post(url, data); //axios的post 默认转化为json格式
  } else if (method === "get") {
    return axios.get(url, {
      params: data
    });
  } else if (method === "delete") {
    return axios.delete(url, {
      params: data
    });
  }
}
