import React, { useState } from 'react'
import { requestLogin, getEnterpriseInfo } from '../../api/login'
import { message, Modal, Table, Button, Input } from 'antd'
import './css/index.css'

interface PropsType {
    history: any;
}

const Login = (props: PropsType) => {
    const [account, setAccount] = useState<string>('');
    const [code, setCode] = useState<string>('');
    const [visible, setVisible] = useState<boolean>(false);
    const [data, setData] = useState<Array<any>>([]);
    const [state, setState] = useState<any>({
        verification: '',
        token: '',

    });

    const login = async (): Promise<any> => {
        let params: any = {
            account,
            code
        }
        let response: any = await requestLogin(params)
        if(response.code === 200) {
            state.token = response.data
            setState({ ...state })

            // props.history.push('/')
            // message.success('登录成功！')
            setVisible(true)
            // setData(res.data.companyInfos)
        } else {
            message.error(response.msg)
        }
    }

    const select = (item: any) => {
        // localStorage.setItem('userInfo', JSON.stringify(item))
        props.history.push('/')
        message.success('登录成功！')
    }

    return (
        <div className="login">
            <div className="login-top">
                <div className='left'>
                    <div className='brandOne'>
                        <img src={require("../../static/images/logo.png")} alt="logo" />
                        <img src={require("../../static/images/logo-desc.png")} alt="logo-desc" />
                    </div>
                    <div className='brandTwo'>
                        <img src={require("../../static/images/brand-1.png")} alt="brand-1" />
                        <img src={require("../../static/images/logo-text.png")} alt="logo-text" />
                    </div>
                    <div className='brandThree' >
                        <img src={require("../../static/images/brand-text.png")} alt="brand-text" />
                    </div>
                </div>
                <div className='right'>
                    <div className='inputBg'>
                        <div className='welcomeText'>欢迎使用随闻面对面，请先登录！</div>
                        <div className='accountInput' style={{ marginBottom: "2.89%" }}>
                            <img src={require("../../static/images/icon-user.png")} alt="icon-user" />
                            <input 
                                className='input' 
                                type="text" 
                                placeholder="请输入用户名"
                                onChange={e => setAccount(e.target.value)}
                                value={account}
                                maxLength={20} />
                        </div>
                        <div className='accountInput' style={{ marginBottom: "6.68%" }}>
                            <img src={require("../../static/images/icon-pwd.png")} alt="icon-pwd" />
                            <input 
                                className='input' 
                                type="password" 
                                placeholder="请输入密码"
                                onChange={e => setCode(e.target.value)}
                                onKeyDown={(e: any) => {
                                    if(e.keyCode === 13) {
                                        login()
                                    }
                                }}
                                value={code}
                                maxLength={20}
                            />
                        </div>
                        <div onClick={() => login()} className='loginBtn'>登录</div>
                    </div>
                </div>
            </div>
            <Modal
                title="验证"
                visible={visible}
                onCancel={() => setVisible(false)}
                onOk={async () => {
                    if(state.verification === 'sw1000') {
                        localStorage.setItem('_token', state.token)
                        let res: any = await getEnterpriseInfo()
                        localStorage.setItem('_user', JSON.stringify(res.data))

                        props.history.push('/')
                        message.success('登录成功！')
                    } else {
                        message.error('验证码错误！')
                    }
                }}
                cancelText="取消"
                okText="确定"
                // okButtonProps={{ style: {display: 'none'} }}
            >
                <Input onChange={(e) => {
                    state.verification = e.target.value
                    setState({ ...state })
                }} value={state.verification} placeholder="请输入验证码" />
            </Modal>
        </div>
    )
}

export default Login
